"use client";

import { useEffect, useRef, useState } from "react";
import { supabase } from "@/lib/supabase";

type Product = {
  id: string | number;
  name: string;
  image?: string | null;
  price?: number | null;
  description?: string | null;
};

export default function Products({
  initialProducts,
  pageSize,
}: {
  initialProducts: Product[];
  pageSize: number;
}) {
  const [products, setProducts] = useState<Product[]>(initialProducts);
  const [loading, setLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const pageRef = useRef(1); // текущая страница, первая уже загружена

  useEffect(() => {
    const handleScroll = () => {
      if (
        window.innerHeight + window.scrollY >=
          document.body.offsetHeight - 200 &&
        !loading &&
        hasMore
      ) {
        fetchNextPage();
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [loading, hasMore]);

  const fetchNextPage = async () => {
    setLoading(true);
    const from = pageRef.current * pageSize;
    const to = from + pageSize - 1;

    const { data } = await supabase
      .from("products")
      .select("*")
      .order("id")
      .range(from, to);

    if (!data || data.length < pageSize) setHasMore(false);

    setProducts((prev) => [...prev, ...(data ?? [])]);
    pageRef.current += 1;
    setLoading(false);
  };

  return (
    <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {products.map((p) => (
          <article
            key={p.id}
            className="bg-white dark:bg-gray-800 rounded-lg overflow-hidden shadow hover:shadow-lg transition"
          >
            <div className="w-full h-48 bg-gray-100 dark:bg-gray-700 overflow-hidden">
              <img
                src={p.image ?? "https://via.placeholder.com/400x300?text=No+Image"}
                alt={p.name}
                className="w-full h-full object-cover"
              />
            </div>

            <div className="p-4">
              <h3 className="text-sm font-medium text-gray-900 dark:text-gray-100 line-clamp-2">
                {p.name}
              </h3>
              {p.description && (
                <p className="mt-2 text-sm text-gray-600 dark:text-gray-300">
                  {p.description}
                </p>
              )}
              <div className="mt-4 text-lg font-semibold text-indigo-600 dark:text-indigo-400">
                {p.price != null ? `${p.price} ₽` : "—"}
              </div>
            </div>
          </article>
        ))}
      </div>

      {loading && <p className="text-center mt-4">Загрузка...</p>}
      {!hasMore && <p className="text-center mt-4">Больше продуктов нет</p>}
    </section>
  );
}
